@import "src/global/variables";

.services {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;

  &_content {
    width: 1200px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
  }
}

.common-services {
  margin: 0 0 80px;
  width: 1200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &_title {
    margin-bottom: 80px;
    font-size: 24px;
    font-weight: 700;
  }

  &_items {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.services-filter {
  display: flex;
  flex-direction: column;
  padding: 20px;
  margin-top: 4px;
  width: 20%;
  min-height: 600px;
  border-radius: 12px;
  background-color: $item-card-background;

  &_price {
    margin-top: 20px;

    &_title {
      font-size: 14px;
      font-weight: 500;
      margin: 12px 0;
    }

    &_wrapper {
      display: flex;
      flex-direction: row;
      align-items: center;
    }

    &_input {
      height: 30px;
      padding: 4px 8px;
      outline: none;
      border: none;
      width: 50%;

      &:first-child {
        border: 1px solid #E3E3E3;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
      }

      &:last-child {
        border: 1px solid #E3E3E3;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
      }
    }
  }

  &_manufacturer {
    margin-top: 12px;

    &_title {
      font-size: 14px;
      font-weight: 500;
      margin: 12px 0;
    }
  }

  &_title {
    font-weight: 500;
  }

  &_search-btn {
    margin-top: 40px;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 28px;
    width: 100%;
    align-self: center;
    color: $white;
    background: linear-gradient(271.38deg, #0B3FC5 -8.46%, #0681E4 75.71%);
    border-radius: 20px;
  }
}