.cart_card {
  padding: 20px 16px;
  width: fit-content;
  background: #F1F0F0;
  box-shadow: 4px 4px 20px rgba(157, 157, 157, 0.33);
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  min-width: 460px;

  &_stage {
    padding-bottom: 2px;
    font-size: 14px;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    border: 1px solid #0A5BD3;
    border-radius: 20px;
  }

  &_content {
    margin: 0 24px;

    &_title {
      min-width: 220px;
      display: flex;
      flex-direction: row;
      align-items: center;
      font-weight: 500;
      text-transform: uppercase;

      img {
        margin-left: 12px;
        width: 18px;
      }
    }

    &_additional {
      margin-top: 20px;
      font-size: 12px;
      color: #A9A5A5;

      span {
        margin-right: 12px;
      }
    }
  }

  &_edit {
    cursor: pointer;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    padding: 0 12px;
    display: flex;
    align-items: center;
    margin-left: 48px;
    color: #095ED4;
  }
}