@import "../../../global/variables";

.popular-items {
  width: 1200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &_title {
    font-size: 24px;
    font-weight: 700;
  }

  &_menu {
    color: $text-secondary;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    &_item {
      cursor: pointer;
      font-size: 12px;
      text-transform: uppercase;
      padding: 0 12px;
    }
  }

  &_content {
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}