.login {
  width: 1200px;

  .cart_card {
    width: 40%;
  }

  .registration_btn {
    width: 40%;
  }

  .registration_btn.secondary_btn {
    background: linear-gradient(271.38deg, #0681E4 -8.46%, rgba(39, 135, 245, 0.91) 75.71%);
  }

  .registration_btn.light_btn {
    background: linear-gradient(271.38deg, #0681E4 -8.46%, rgba(39, 135, 245, 0.72) 75.71%);
  }

  &_forget {
    margin-top: 20px;

    span {
      cursor: pointer;
      color: #095ED4;
    }
  }

  &_title {
    margin: 20px 0;
    text-align: center;
    font-size: 24px;
    font-weight: 500;
  }

  &_wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}