@import "../../../global/variables";

.active_menu_item {
  color: $active-menu-item;
}

.item_card {
  width: 300px;
  display: flex;
  flex-direction: column;
  background-color: $item-card-background;
  padding: 20px 32px;
  margin: 4px;

  &_image {
    width: 100%;
  }

  &_favorite {
    font-size: 22px;
  }

  &_wrapper {
    display: flex;
    flex-direction: column;
  }

  &_availability {
    font-size: 12px;

    &_content {
      display: flex;
      flex-direction: row;
      align-items: center;

      img {
        margin: 1px 4px 0 0;
      }
    }
  }

  &_cart {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 10px;
    color: $white;
    background: linear-gradient(271.38deg, #0B3FC5 -8.46%, #0681E4 75.71%);
    padding: 4px 8px;
    border-radius: 10px;
  }

  &_content {
    display: flex;
    flex-direction: column;
    padding: 12px 16px;
    margin-top: 40px;
    background-color: $white;
    border-radius: 20px;

    &_more {
      font-size: 10px;
      width: 90px;
      padding: 8px;
      text-align: center;
      color: $white;
      border-radius: 10px;
      background: linear-gradient(271.38deg, #0B3FC5 -8.46%, #0681E4 75.71%);
    }

    &_status {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }

    &_title {
      margin: 12px 0;
      font-size: 16px;
    }

    &_price {
      color: $text-blue;
      font-weight: 500;
    }
  }

  &_favorite {
    margin-bottom: 20px;
    text-align: right;
  }
}