@import "src/global/variables";

.footer {
  margin: 80px auto 20px;
  width: 1200px;
  display: flex;
  flex-direction: column;

  &_title {
    font-size: 24px;
    font-weight: 500;

    &__colored {
      color: #305878;
    }
  }

  &_content {
    margin: 60px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  &_contact-info {
    width: 35%;

    &_title {
      font-size: 18px;
      font-weight: 500;
      margin-bottom: 20px;
    }
  }

  &_contact_info_item_title {
    font-weight: 500;
    padding: 4px 0;
    color: $footer_secondary_color;
  }

  &_contact_info_item_text {
    font-size: 14px;
    margin: 4px 0;
  }

  &_navigation {
    width: 65%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    &_sub-item-title {
      font-size: 18px;
      font-weight: 500;
      margin-bottom: 20px;
    }

    &_sub-item {
      font-size: 14px;
      margin: 4px 0;
    }
  }

  &_text {
    font-size: 14px;
    font-weight: 500;
    text-align: center;
  }
}