.registration {
  display: flex;
  flex-direction: column;
  width: 1200px;

  &_btn {
    margin-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 280px;
    height: 40px;
    color: #FFFFFF;
    background: linear-gradient(271.38deg, #0B3FC5 -8.46%, #0681E4 75.71%);
    border-radius: 12px;
    transition: .3s;

    &:hover {
      cursor: pointer;
      opacity: .8;
    }
  }

  &_wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  &_title {
    margin: 40px 0;
    font-size: 24px;
    font-weight: 500;
  }

  &_group {
    display: flex;
    flex-direction: column;
    width: 40%;

    label {
      margin: 16px 0;
    }

    input {
      border: none;
      outline: none;
      padding: 16px 20px;
      background: #F1F0F0;
      box-shadow: 4px 4px 20px rgba(157, 157, 157, 0.25);
      border-radius: 20px;
      margin-bottom: 20px;
    }
  }
}