.about {
  margin-top: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  &_map {
    margin-bottom: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1200px;
    height: 420px;
    overflow: hidden;

    div:first-child {
      width: 100% !important;
      height: 100% !important;
    }
  }
}

