@import "src/global/variables";

.swiper-wrapper {
  padding-right: 16%;
  display: flex;
  align-items: center;
}

.swiper-slide-next {
  width: 50% !important;
}

.swiper-slide-next .slider_slide {
  font-size: 18px;
}

.swiper-slide-next .slider_text {
  text-align: left;
  margin: 80px 0;
}

.swiper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  &_slide {
    font-size: 10px;

    transition: .3s;

    width: 100%;
    height: 100%;
    color: $white;
    padding: 100px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
    border-radius: 20px;

    margin: 0 !important;

    text-align: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;

    img {
      width: 50%;
    }
  }

  &_title {
    text-align: left;
    font-size: 3.75em;
    font-weight: 700;
  }

  &_text {
    text-align: left;
    margin: 20px 0;
    font-size: 1.25em;
  }

  &_btn {
    font-size: 1em;
    border-radius: 30px;
    padding: 12px 24px;
    width: fit-content;
    background: $btn-color;
    box-shadow: 0px 0px 29px 11px rgba(52, 100, 223, 0.64);
  }
}