.cart_order {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  padding: 12px 32px;
  background: #F1F0F0;
  box-shadow: 4px 4px 20px rgba(157, 157, 157, 0.33);
  border-radius: 20px;

  &_title {
    font-weight: 500;
    border-bottom: 2px solid #D6D6D6;
    padding: 12px;
  }

  &_items {
    display: flex;
    flex-direction: column;
  }

  &_price_amount {
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &_item {
    padding-bottom: 16px;
    border-bottom: 2px solid #D6D6D6;
    margin: 16px 0;
    display: flex;
    flex-direction: row;
    align-items: center;

    &_description {
      min-width: 240px;
      padding-left: 20px;
    }

    &_title {
      font-weight: 500;
    }

    &_model {
      font-size: 14px;
      padding: 8px 0;
    }

    &_image {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #F1F0F0;
      box-shadow: 0 4px 20px rgba(0, 0, 0, 0.11);
      border-radius: 20px;
      width: 40%;

      img {
        width: 80%;
      }
    }

    &_price {
      color: #095ED4;
      font-weight: 500;
    }

    &_amount {
      font-size: 14px;
      margin-left: 20px;
    }
  }

  &_additional-info {
    border-bottom: 2px solid #D6D6D6;
  }

  &_additional_item {
    margin: 20px 0;

    span {
      font-size: 14px;
      display: inline-block;
      &:first-child {
        width: 65%;
      }
      &:last-child {
        font-weight: 500;
        color: #095ED4;
      }
    }
  }

  &_overall {
    padding: 20px 0;
    span {
      font-weight: 500;
      display: inline-block;
      &:first-child {
        width: 65%;
      }
      &:last-child {
        color: #095ED4;
      }
    }
  }

  &_confirm-order {
    margin-top: 20px;
    padding: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FFF;
    background: linear-gradient(271.38deg, #0B3FC5 -8.46%, #0681E4 75.71%);
    border-radius: 20px;
    transition: .35s;

    &:hover {
      cursor: pointer;
      opacity: 0.8;
    }
  }
}