.nav-panel {
  width: 1200px;
  display: flex;
  flex-direction: row;
  margin-top: 120px;

  &_item {
    padding: 0 4px;
    display: flex;
    flex-direction: row;

    &-circle {
      border: 1px solid #7B7272;
      background: #7B7272;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      margin-top: auto;
      margin-right: 12px;
      margin-bottom: auto;
      margin-left: 4px;
    }
  }

  &_wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}