.item-page {
  display: flex;
  justify-content: center;
  align-items: center;

  &_image {
    width: 40%;

    img {
      width: 90%;
    }
  }

  &_wrapper {
    width: 1200px;
    display: flex;
    flex-direction: column;
  }

  &_content {
    margin-top: 40px;
    display: flex;
    flex-direction: row;
  }

  &_configuration {
    margin-top: 20px;
    display: flex;
    flex-direction: column;

    &_group {
      margin: 12px 0;
      display: flex;
      flex-direction: column;

      &_title {
        color: #9C9C9C;
        margin: 12px 0;
      }
    }

    &_items {
      display: flex;
      flex-direction: row;

      div {
        margin: 0 8px;

        &:first-child {
          margin: 0;
          margin-right: 8px;
        }
      }
    }
  }

  &_title {
    font-size: 24px;
    font-weight: 500;
  }

}

.color_config {
  font-size: 0;
  width: 28px;
  height: 28px;
  border-radius: 50px;
}

.config-item-block {
  font-size: 14px;
  font-weight: 500;
  padding: 8px 12px;
  background: rgba(234, 234, 236, 0.55);
  border-radius: 20px;

  &-active {
    color: #FFFFFF;
    background: rgba(9, 96, 212, 0.82);
    box-shadow: 0px 0px 29px 11px rgba(52, 100, 223, 0.26);
    border-radius: 20px;
  }
}