@import '../../../global/variables';

.header {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  color: $white;
  background-color: $primary-blue;
  box-shadow: 0 4px 23px 0 #00000040;

  img.header_wrapper_control-panel_search_icon {
    right: 8px;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    height: 16px;
    width: 16px;
  }

  &_wrapper {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 1200px;
    height: 100%;
    margin: 0 60px;

    &_logo {
      width: 25%;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;

      img {
        padding-top: 4px;
        height: 42px;
      }

      span {
        padding: 0 4px;
        font-size: 24px;
        text-transform: uppercase;
      }
    }

    &_navigation {
      width: 50%;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      font-size: 18px;

      &_item {
        color: $white;
        padding: 0 18px;
      }
    }

    &_control-panel {
      width: 25%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

      div {
        cursor: pointer;
        margin: 0 8px;
      }

      div:not(:first-child) {
        font-size: 24px;
      }

      &_search {
        position: relative;
        width: 280px;
        height: 32px;

        input {
          padding: 0 12px;
          outline: none;
          border: none;
          border-radius: 15px;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}