.content_block {
  padding: 20px 24px;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 24px 0;

  &--colored {
    color: #FFF;
    background: linear-gradient(271.38deg, #0B3FC5 -8.46%, #0681E4 75.71%);
    border-radius: 20px;
  }

  &_image_wrapper {
    width: 90%;
  }

  img {
    width: 100%;
  }

  &_text {
    padding: 24px;
    font-size: 20px;

    div {
      margin: 12px 0;
    }
  }
}