@import "src/global/variables";

.category_card {
  display: flex;
  flex-direction: column;
  padding: 20px 24px;
  background-color: $item-card-background;
  transition: .3s;
  border-radius: 12px;

  &:hover {
    box-shadow: 0 0 12px 5px rgba(52, 100, 223, 0.6);
  }

  &_title {
    font-size: 26px;
    font-weight: 600;
  }

  &_amount {
    margin: 20px 0;
    font-size: 14px;
    font-weight: 500;
  }

  &_link {
    font-weight: 600;

    a {
      display: flex;
      flex-direction: row;
      align-items: center;
      color: $link-color;
      font-size: 14px;

      span {
        padding-right: 8px;
      }
    }
  }

  &_image {
    margin: 0;
  }
}