@import "src/global/variables";

.compare {
  margin-top: 60px;
  display: flex;
  width: 1200px;
  flex-direction: column;

  &_wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  &_title {
    text-align: center;
    font-size: 24px;
    font-weight: 500;
  }

  &_sub-title {
    margin-top: 16px;
    font-size: 14px;
    text-align: center;
    color: $active-menu-item;
  }

  &_goods {
    margin-top: 40px;
    display: flex;
    flex-direction: row;

    &_gap {
      width: 40%;
    }

    &_content {
      display: flex;
      flex-direction: row;
    }
  }

  &_table {

  }
}