.item-info {
  margin: 80px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 1200px;

  &_content {
    width: 100%;
  }

  &_title {
    font-size: 24px;
    font-weight: 500;
  }

  &_menu {
    display: flex;
    flex-direction: row;

    div {
      margin: 12px 8px;
    }
  }
}