.favorite-items {
  display: flex;
  flex-direction: column;
  width: 1200px;

  &_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &_title {
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    margin: 60px 0 40px;
  }

  &_content {
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}