.review {
  margin-bottom: 60px;
  display: flex;
  flex-direction: column;

  &_no-comments {
    font-weight: 500;
    margin: 40px 0;
  }

  &_own {
    &_title {
      margin: 8px 0 20px;
      font-size: 18px;
      font-weight: 500;
    }

    &_text {
      padding: 16px;
      border: none;
      outline: none;
      width: 50%;
      min-height: 200px;
      resize: none;
      background: #F1F0F0;
      box-shadow: 4px 4px 20px rgba(157, 157, 157, 0.25);
      border-radius: 20px;
    }
  }

  &_submit-btn {
    margin-top: 40px;
    font-size: 14px;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FFFFFF;
    height: 40px;
    width: 280px;
    background: linear-gradient(271.38deg, #0B3FC5 -8.46%, #0681E4 75.71%);
    border-radius: 20px;
  }
}

.colored_span {
  cursor: pointer;
  color: #3763FF;
}