.service-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &_title {
    margin: 40px 0;
    text-align: center;
    font-size: 24px;
    font-weight: 700;
  }

  &_content {
    margin-top: 80px;
    width: 1200px;
    display: flex;
    flex-direction: column;
  }

  &_preview {
    display: flex;
    flex-direction: row;

    img {
      width: 70%;
    }

    &_description {
      font-size: 24px;
      font-weight: 500;
    }
  }

  &_subtitle {
    margin: 60px 0 12px;
  }

  &_menu {
    display: flex;
    flex-direction: row;
    justify-content: center;

    &_item {
      font-size: 14px;
      margin: 0 8px;
    }
  }
}