.billing {
  display: flex;
  flex-direction: column;
  width: 1200px;

  &_wrapper {
    margin-top: 40px;
    display: flex;
    justify-content: center;
    width: 100%;
  }

  &_title {
    margin: 20px 0;
    font-size: 20px;
    font-weight: 500;
  }

  &_text_item {
    margin: 12px 0;

    span {
      font-weight: 500;
    }
  }
}