.cart_pickup {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  &_payment-type-form,
  &_delivery-type-form {
    display: flex;
    flex-direction: column;

    div {
      margin: 4px 0;
    }
  }

  &_type_title {
    font-weight: 500;
  }

  &_info {

    .cart_card {
      margin: 50px 0;
    }
  }
}