@import "src/global/variables";

.contact {
  width: 1200px;
  margin: 80px 0;

  &_content {
    display: flex;
    flex-direction: row;
  }

  &_title {
    margin: 0 0 60px;
    font-size: 24px;
    font-weight: 700;
    text-align: center;
  }

  &_contact_info_container {
    width: 40%;
  }

  &_form_container {
    width: 60%;
  }
}

.contact_info {
  &_group {
    display: flex;
    flex-direction: column;
    margin: 12px 0;
  }

  &_item {
    font-weight: 600;
    margin: 12px 0;

    div {
      display: inline-block;
      width: 20px;
    }
  }
}

.contact_form {
  padding: 40px 80px;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: $item-card-background;
  box-shadow: 0 4px 23px 6px rgba(0, 0, 0, 0.15);
  border-radius: 20px;

  &_title {
    text-align: center;
    margin-bottom: 40px;
    font-size: 18px;
    font-weight: 700;
  }

  &_group {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 30px;

    label {
      padding-top: 10px;
      width: 30%;
      font-weight: 500;
    }

    input {
      padding: 12px 20px;
      width: 70%;
      height: 40px;
      border: 1px solid rgba(141, 138, 138, 0.4);
      box-shadow: 0 0 12px rgba(0, 0, 0, 0.1);
      border-radius: 20px
    }

    textarea {
      resize: none;
      width: 70%;
      height: 120px;
      border: 1px solid rgba(141, 138, 138, 0.4);
      box-shadow: 0 0 12px rgba(0, 0, 0, 0.1);
      border-radius: 20px;
      padding: 12px 20px;
    }
  }

  &__submit_btn {
    align-self: flex-end;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    width: 200px;
    height: 40px;
    color: $white;
    background: linear-gradient(271.38deg, #0B3FC5 -8.46%, #0681E4 75.71%);
    border-radius: 20px;
  }
}